/**
 * #.# Editor Styles
 *
 * CSS for just Backend
 * Enqueued after style.scss which makes it higher in priority.
 *
 */

.wp-block-cognito-forms-cognito-embed {
	.components-placeholder__label svg {
		height: 24px;
		width: 24px;
	}

	.form-preview {
		min-height: 200px;

		.focus-grabber {
			position: absolute;
			width: 100%;
			height: 100%;
			z-index: 1;
		}

		.loader {
			padding: 1em;
			background: #fff;
			text-align: center;
			min-height: 200px;
			border-radius: 2px;
			box-shadow: inset 0 0 0 1px #1e1e1e;

			display: flex;
			gap: 0.5em;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			.icon {
				width: 2em;
				height: 2em;
				margin: 0 auto;
			}

			p {
				font-size: 13px;
				margin: 0;
			}
		}
	}
}

// Remove modal padding
.cognito-modal {
	height: 600px;

	.components-modal__content {
		padding: initial;
	}

	.components-modal__content::before {
		display: none;
	}

	.components-modal__content .components-modal__header {
		margin: initial;
	}
}
